import styles from './calendar.module.scss'

const weekDay = ['일', '월', '화', '수', '목', '금', '토']

const Calendar = () => {
  return (
    <section className={styles.section}>
      <h2>WEDDING DAY</h2>
      <p className={styles.date}>2024. 6. 22. (토) 오후 3시</p>
      <div className={styles.calendar}>
        <div className={styles.month}>6월</div>
        <table>
          <tbody>
            <tr className={styles.dayOfWeek}>
              {weekDay.map((week, index) => {
                return <th key={index}>{week}</th>
              })}
            </tr>
            <tr>
              <td colSpan={6}>&nbsp;</td>
              <td>
                <span>1</span>
              </td>
            </tr>
            <tr>
              <td>
                <span>2</span>
              </td>
              <td>
                <span>3</span>
              </td>
              <td>
                <span>4</span>
              </td>
              <td>
                <span>5</span>
              </td>
              <td>
                <span>6</span>
              </td>
              <td>
                <span>7</span>
              </td>
              <td>
                <span>8</span>
              </td>
            </tr>
            <tr>
              <td>
                <span>9</span>
              </td>
              <td>
                <span>10</span>
              </td>
              <td>
                <span>11</span>
              </td>
              <td>
                <span>12</span>
              </td>
              <td>
                <span>13</span>
              </td>
              <td>
                <span>14</span>
              </td>
              <td>
                <span>15</span>
              </td>
            </tr>
            <tr>
              <td>
                <span>16</span>
              </td>
              <td>
                <span>17</span>
              </td>
              <td>
                <span>18</span>
              </td>
              <td>
                <span>19</span>
              </td>
              <td>
                <span>20</span>
              </td>
              <td>
                <span>21</span>
              </td>
              <td className={styles.dday}>
                <span>22</span>
              </td>
            </tr>
            <tr>
              <td>
                <span>23</span>
              </td>
              <td>
                <span>24</span>
              </td>
              <td>
                <span>25</span>
              </td>
              <td>
                <span>26</span>
              </td>
              <td>
                <span>27</span>
              </td>
              <td>
                <span>28</span>
              </td>
              <td>
                <span>29</span>
              </td>
            </tr>
            <tr>
              <td>
                <span>30</span>
              </td>
              <td colSpan={6}>&nbsp;</td>
            </tr>
          </tbody>
        </table>
      </div>
    </section>
  )
}

export default Calendar
