import Button from 'components/Button'
import { useModal } from 'hooks/useModal'
import Modal from 'components/Modal'
import { ReactComponent as MailIcon } from 'assets/icons/mail.svg'
import { ReactComponent as PhoneIcon } from 'assets/icons/telephone.svg'
import { MouseEvent, useState } from 'react'
import styles from './welcome.module.scss'

const connect = {
  bride: '신부측',
  groom: '신랑측',
}

type Connect = typeof connect[keyof typeof connect]

interface Account {
  person: string
  name: string
  connect: string
}

const accounts: Record<keyof typeof connect, Account[]> = {
  groom: [
    {
      person: '신랑',
      name: '최병근',
      connect: '010-9560-7776',
    },
    {
      person: '신랑 아버지',
      name: '최정락',
      connect: '010-0000-0000',
    },
    {
      person: '신랑 어머니',
      name: '이연분',
      connect: '010-4323-7661',
    },
  ],
  bride: [
    {
      person: '신부',
      name: '선인장',
      connect: '010-9692-9591',
    },
    {
      person: '신부 아버지',
      name: '선연호',
      connect: '010-6624-9591',
    },
    {
      person: '신부 어머니',
      name: '김맹숙',
      connect: '010-8225-9591',
    },
  ],
}

const Welcome = () => {
  const [selectedSide, setSelectedSide] = useState<Connect>(connect.groom)
  const { isModalOpen, openModal, closeModal } = useModal()

  const handleClick = (e: MouseEvent<HTMLButtonElement>) => {
    setSelectedSide((e.target as HTMLButtonElement).value as Connect)
    openModal()
  }

  return (
    <section className={styles.welcome}>
      <h2>INVITATION</h2>
      <h3>초대장</h3>
      <p>
        은혜 안에서 만난 저희 두 사람이
        <br />
        믿음의 가정을 이루는 첫 걸음을 내딛습니다.
        <br />
        하나님의 사랑을 주위에 나누며
        <br />
        살아가는 가정이 되도록
        <br />
        축복과 격려로 함께하여 주시면 감사하겠습니다.
      </p>
      <div className={styles.host}>
        <div>
          <div>
            <span>최정락 · 이연분</span> 의 장남
          </div>
          <span>병근</span>
        </div>
        <div>
          <div>
            <span>선연호 · 김맹숙</span> 의 장녀
          </div>
          <span>인장</span>
        </div>
        <div className={styles.buttonWrapper}>
          <Button text='연락하기' value='연락하기' size='fullWidth' buttonStyle='secondary' onClick={handleClick} />
        </div>
        {isModalOpen && (
          <Modal title='연락하기' closeModal={closeModal}>
            <div className={styles.connect}>
              {accounts.groom.map((account) => (
                <div key={account.name} className={styles.connectItem}>
                  <span>{account.person}</span>
                  <span>{account.name}</span>
                  <span>
                    <a href={`tel:${account.connect}`}>
                      <PhoneIcon />
                    </a>
                    <a href={`sms:${account.connect}`}>
                      <MailIcon />
                    </a>
                  </span>
                </div>
              ))}
              {accounts.bride.map((account) => (
                <div key={account.name} className={styles.connectItem}>
                  <span>{account.person}</span>
                  <span>{account.name}</span>
                  <span>
                    <a href={`tel:${account.connect}`}>
                      <PhoneIcon />
                    </a>
                    <a href={`sms:${account.connect}`}>
                      <MailIcon />
                    </a>
                  </span>
                </div>
              ))}
            </div>
          </Modal>
        )}
      </div>
    </section>
  )
}

export default Welcome
