import styles from './information.module.scss'

const Information = () => {
  const InformationText = [
    {
      title: '탑승장소',
      description: '광주 광산구 금봉로 106 (우산동 1621)\n세계수영선수촌 중흥S클래스 123동 앞 큰 도로가',
    },
    {
      title: '탑승일시',
      description: '2024년 6월 22일 토요일 오전 8시',
    },
  ]

  return (
    <section>
      <h2>INFORMATION</h2>
      <h3>전세 버스 안내</h3>
      <div className={styles.information}>
        {InformationText.map((text) => (
          <div key={text.title} className={styles.InformationItem}>
            <h4>{text.title}</h4>
            <p>{text.description}</p>
          </div>
        ))}
      </div>
    </section>
  )
}

export default Information
