import { useEffect, useRef, useState } from 'react'
import styles from './location.module.scss'
import kakaoIcon from 'assets/icons/kakao_map.png'
import naverIcon from 'assets/icons/naver_map.png'

const addressText = [
  {
    title: '주소',
    description: '서울시 강서구 강서로 388 (등촌동 678-14)',
  },
  {
    title: '지하철',
    description: '5호선 발산역 3번 출구 방향 도보 1분\n9호선 양천향교역 6번 춯구 방향 도보 10분',
  },
  {
    title: '버스',
    description:
      '발산역 정류장 하차\n[지선버스] 6630/6632/6642/5548/6657\n[간선버스] 601/605/652/654/661\n[일반버스] 60/60-3/88/1002\n[직행버스] 3000/8000\n[공항버스] 6003',
  },
  {
    title: '주차 안내',
    description:
      "네비에 '베뉴지웨딩 주차장' 검색\n* 주차요금 사전정산 후 출차\n(2시간 무료, 엘리베이터 앞 무인정산기 이용)",
  },
]

const Location = () => {
  const [currentIndex, setCurrentIndex] = useState(0)
  const mapElement = useRef<HTMLDivElement>(null)

  useEffect(() => {
    if (!mapElement.current) return

    const center = new naver.maps.LatLng(37.5601837, 126.8393044)
    const location = new naver.maps.LatLng(37.5601837, 126.8393044)

    const mapOptions: naver.maps.MapOptions = {
      center,
      zoom: 16,
      zoomControl: false,
      disableDoubleTapZoom: true,
      disableDoubleClickZoom: true,
      disableTwoFingerTapZoom: true,
      pinchZoom: false,
      draggable: false,
      scrollWheel: false,
    }
    const map = new naver.maps.Map(mapElement.current, mapOptions)
    // eslint-disable-next-line unused-imports/no-unused-vars
    const marker = new naver.maps.Marker({
      position: location,
      map,
    })
  }, [])

  return (
    <section>
      <h2>LOCATION</h2>
      <h3>오시는 길</h3>
      <h3 className={styles.location}>더 베뉴지 서울 1층 네이처홀</h3>
      <div ref={mapElement} className={styles.map} />
      <div className={styles.mapIcons}>
        <a href='https://naver.me/xGOibyPL' target='_blank' rel='noreferrer'>
          <img src={naverIcon} alt='네이버 지도' />
        </a>
        <a href='https://kko.to/_D8D74QpUH' target='_blank' rel='noreferrer'>
          <img src={kakaoIcon} alt='카카오 지도' />
        </a>
      </div>
      <div className={styles.address}>
        {addressText.map((text) => (
          <div key={text.title} className={styles.addressItem}>
            <h4>{text.title}</h4>
            <p>{text.description}</p>
          </div>
        ))}
      </div>
    </section>
  )
}

export default Location
