import { MouseEvent, useEffect, useState } from 'react'

import { useModal } from 'hooks/useModal'
import Modal from 'components/Modal'
import Button from 'components/Button'
import { ReactComponent as CheckIcon } from 'assets/icons/check.svg'
import kakaoPayIcon from 'assets/icons/payment_icon_yellow_small.png'
import styles from './bankAccount.module.scss'

const side = {
  bride: '신부측',
  groom: '신랑측',
}

type Side = typeof side[keyof typeof side]

interface Account {
  person: string
  name: string
  bank: string
  account: string
  kakaopay: string
}

const accounts: Record<keyof typeof side, Account[]> = {
  groom: [
    {
      person: '신랑',
      name: '최병근',
      bank: '하나은행',
      account: '000-000-000000',
      kakaopay: '',
    },
    {
      person: '아버지',
      name: '최정락',
      bank: 'OO은행',
      account: '000-000-000000',
      kakaopay: '',
    },
    {
      person: '어머니',
      name: '이연분',
      bank: 'OO은행',
      account: '000-000-000000',
      kakaopay: '',
    },
  ],
  bride: [
    {
      person: '신부',
      name: '선인장',
      bank: '국민은행',
      account: '793302-01-158411',
      kakaopay: 'https://qr.kakaopay.com/Ej9JOKLUx',
    },
    {
      person: '아버지',
      name: '선연호',
      bank: '농협은행',
      account: '601011-56-041277',
      kakaopay: '',
    },
  ],
}

const BankAccount = () => {
  const [selectedSide, setSelectedSide] = useState<Side>(side.groom)
  const [copiedPerson, setCopiedPerson] = useState('')
  const { isModalOpen, openModal, closeModal } = useModal()

  useEffect(() => {
    return () => setCopiedPerson('')
  }, [isModalOpen])

  const handleClick = (e: MouseEvent<HTMLButtonElement>) => {
    setSelectedSide((e.target as HTMLButtonElement).value as Side)
    openModal()
  }

  const handleCopy = (e: MouseEvent<HTMLButtonElement>) => {
    const { id, value } = e.target as HTMLButtonElement
    navigator.clipboard.writeText(value)
    setCopiedPerson(id)
  }

  return (
    <section className={styles.accountWrapper}>
      <h3>마음 전하실 곳</h3>
      <div className={styles.buttonWrapper}>
        <Button text={side.groom} value={side.groom} size='fullWidth' buttonStyle='secondary' onClick={handleClick} />
        <Button text={side.bride} value={side.bride} size='fullWidth' buttonStyle='secondary' onClick={handleClick} />
      </div>
      {isModalOpen && (
        <Modal title={selectedSide === side.groom ? side.groom : side.bride} closeModal={closeModal}>
          {accounts[selectedSide === side.groom ? 'groom' : 'bride'].map((account) => (
            <div key={account.name} className={styles.accountItem}>
              <div className={styles.accountList}>
                <div className={styles.name}>
                  {account.person} <span>{account.name}</span>
                </div>
                <div className={styles.account}>
                  {account.bank} {account.account}
                </div>
              </div>
              <div className={styles.accountMethod}>
                {account.name === copiedPerson ? (
                  <CheckIcon />
                ) : (
                  <Button
                    className={styles.copyButton}
                    id={account.name}
                    text='계좌복사'
                    value={`${account.bank} ${account.account}`}
                    size='small'
                    buttonStyle='ghost'
                    onClick={handleCopy}
                  />
                )}
                <a
                  href={account.kakaopay}
                  target='_blank'
                  rel='noreferrer'
                  className={!account.kakaopay ? styles.disabled : ''}
                >
                  <img
                    src={kakaoPayIcon}
                    alt='카카오페이 송금'
                    width={70}
                    className={!account.kakaopay ? styles.disabledKaKaoPay : ''}
                  />
                </a>
              </div>
            </div>
          ))}
        </Modal>
      )}
    </section>
  )
}

export default BankAccount
