import { supabase } from './supabase'
import { MessageDTO } from 'types/supabase'

export const getMessages = async () => {
  const { data } = await supabase.from('wedding_board').select().order('id', { ascending: false })

  return { messages: data || [] }
}

export const postMessage = async (message: Omit<MessageDTO, 'id'>) => {
  const { data, error } = await supabase
    .from('wedding_board')
    .insert({ ...message })
    .select()

  return { message: data || [], error }
}

export const deleteMessage = async (id: number) => {
  const { error } = await supabase.from('wedding_board').delete().eq('id', id)

  return { error }
}
