import { MouseEvent, useState } from 'react'
import { useModal } from 'hooks/useModal'
import ImageGrid from 'components/ImageGrid'
import ImageViewer from 'components/ImageViewer'
import ImageCarousel from 'components/ImageCarousel'
import image01 from 'assets/images/weddingphoto_01.jpg'
import image02 from 'assets/images/weddingphoto_02.jpg'
import image03 from 'assets/images/weddingphoto_03.jpg'
import image04 from 'assets/images/weddingphoto_04.jpg'
import image05 from 'assets/images/weddingphoto_05.jpg'
import image06 from 'assets/images/weddingphoto_06.jpg'
import image07 from 'assets/images/weddingphoto_07.jpg'
import image08 from 'assets/images/weddingphoto_08.jpg'
import image09 from 'assets/images/weddingphoto_09.jpg'
import image10 from 'assets/images/weddingphoto_10.jpg'

const imageList = [image01, image02, image03, image04, image05, image06, image07, image08, image09, image10]

const Gallery = () => {
  const [currentIndex, setCurrentIndex] = useState(0)
  const { isModalOpen, openModal, closeModal } = useModal()

  const handleImageClick = (e: MouseEvent) => {
    const index = Number((e.target as HTMLImageElement).dataset.index)
    setCurrentIndex(index)
    openModal()
  }

  return (
    <section>
      <h2>GALLERY</h2>
      <h3>갤러리</h3>
      <ImageGrid imageList={imageList} handleImageClick={handleImageClick} />
      {isModalOpen && (
        <ImageViewer
          imageList={imageList}
          currentIndex={currentIndex}
          setCurrentIndex={setCurrentIndex}
          close={closeModal}
          hasButton={false}
        >
          <ImageCarousel
            images={imageList}
            currentIndex={currentIndex}
            setCurrentIndex={setCurrentIndex}
            hasIndicator={false}
            autoPlay={false}
            hasPadding
          />
        </ImageViewer>
      )}
    </section>
  )
}

export default Gallery
